import React from "react";
import {
  
  TextField,
  FormControl,
  FormHelperText,
  InputLabel,
  Select,
  MenuItem,
  Button,

  
} from "@material-ui/core";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";


import DateFnsUtils from "@date-io/date-fns";
import { makeStyles } from "@material-ui/core/styles";
import { useForm, Controller } from "react-hook-form";


const useStyles = makeStyles((theme) => ({
  inputField: {
    width: "100%",
    margin: theme.spacing(1, 0),
  },
}));
//defual funtion


const Home = () =>
 {
  const classes = useStyles();
  const { register, handleSubmit, control, errors } = useForm();
  const onSubmit = (data) => console.log(data);
  console.log(errors);
  return (
  

    
        <form 
        onSubmit={handleSubmit(onSubmit)}>
          {/* 1) TextField */}
          <TextField
            placeholder="Enter Your Aadhar card No"
            label="Aadhar Card No"
            variant="outlined"
            fullWidth
            className={classes.inputField}
            name=" Enter Your Aadhar No"
            inputRef={register({
              required: "Aadhar No is required.",
            })}
            error={Boolean(errors.Aadharno)}
            helperText={errors.Aadharno?.message}
          />

          {/* 2) TextField */}
          <TextField
            placeholder="Enter Your Name"
            label="Enter Your Name"
            variant="outlined"
            fullWidth
            className={classes.inputField}
            name="Enter Your Name"
            inputRef={register({
              required: "Name is required.",
            })}
            error={Boolean(errors.Name)}
            helperText={errors.Name?.message}
          />

          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            {/* 5) Date Picker */}
            <Controller
              render={(props) => (
                <KeyboardDatePicker
                  disableToolbar
                  variant="inline"
                  format="MM/dd/yyyy"
                  margin="normal"
                  label="Enter Your DOB"
                  value={props.value}
                  onChange={props.onChange}
                  fullWidth
                  error={Boolean(errors.Enter_Your_DOB)}
                  helperText={errors.Enter_Your_BOD?.message}
                />
              )}
              name="Enter_Your_DOB"
              defaultValue={null}
              control={control}
              rules={{
                required: "Your BOD is required.",
              }}
            />

         </MuiPickersUtilsProvider>

            {/* Select */}
            <FormControl
            fullWidth
            className={classes.inputField}
            error={Boolean(errors.course)}
          >
            <InputLabel id="demo-simple-select-label">
              Select Your Gender
            </InputLabel>
          
            <Controller
              render={(props) => (
                <Select value={props.value} onChange={props.onChange}>
                  <MenuItem value="">Select Your Cast</MenuItem>
                  <MenuItem value="Male">Male</MenuItem>
                  <MenuItem value="Female">Female</MenuItem>
                  <MenuItem value="Other">Other</MenuItem>
                  
                </Select>
              )}
              name="Gender"
              control={control}
              defaultValue=""
              rules={{
                required: "please Select Your Gender.",
              }}
            />
            <FormHelperText>{errors.Gender?.message}</FormHelperText>
          </FormControl>

          

          {/* Select */}
          <FormControl
            fullWidth
            className={classes.inputField}
            error={Boolean(errors.course)}
          >
            <InputLabel id="demo-simple-select-label">
              Select Your cast
            </InputLabel>
          
            <Controller
              render={(props) => (
                <Select value={props.value} onChange={props.onChange}>
                  <MenuItem value="">Select Your Cast</MenuItem>
                  <MenuItem value="Open">Open</MenuItem>
                  <MenuItem value="OBC">OBC</MenuItem>
                  <MenuItem value="NT">NT</MenuItem>
                  <MenuItem value="SC">SC</MenuItem>
                </Select>
              )}
              name="cast"
              control={control}
              defaultValue=""
              rules={{
                required: "please choose your cast.",
              }}
            />
            <FormHelperText>{errors.cast?.message}</FormHelperText>
          </FormControl>

            {/* Select */}
            <FormControl
            fullWidth
            className={classes.inputField}
            error={Boolean(errors.course)}
          >
            <InputLabel id="demo-simple-select-label">
              Disability Yes / NO
            </InputLabel>
          
            <Controller
              render={(props) => (
                <Select value={props.value} onChange={props.onChange}>
                  <MenuItem value="">Disability Yes / No</MenuItem>
                  <MenuItem value="Yes">Yes</MenuItem>
                  <MenuItem value="No">No</MenuItem>
                </Select>
              )}
              name="cast"
              control={control}
              defaultValue=""
              rules={{
                required: "please choose your cast.",
              }}
            />
            <FormHelperText>{errors.cast?.message}</FormHelperText>
          </FormControl>

          
          <Button variant="contained" color="primary" type="submit" >
            submit
          </Button>
        </form>
        
        
  )
  
            };
          
           
           
 export default Home;


        



